<template>
  <div class="product_list">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>home</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/product/infolist' }"
          >信息列表</el-breadcrumb-item
        >
      </el-breadcrumb>
    </div>
    <div class="infomation_main">
      <div class="infomation" v-for="(item, index) of 16" :key="index">
        <img class="infomation_icon" src="../../assets/img/group.png" />
        <div class="infomation_txt">
          海关总署
          国家外汇管理局公告2019年93号（关于取消报关单收、付汇证明联和海关核销联的公告）
        </div>
        <div class="infomation_date">2020-05-27</div>
      </div>
      <div class="paginaton">
        <el-pagination
          :background="false"
          :pager-count="9"
          layout="prev, pager, next"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :total="80"
        >
        </el-pagination>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
  },
  created() {},
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #eeeeee;
}
.bottom {
  height: 24px;
}
.breadcrumb {
  width: 1440px;
  margin: 0 auto;
  height: 68px;
  padding-left: 146px;
}
.el-breadcrumb__item {
  height: 68px;
  line-height: 68px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
}
.el-breadcrumb__item:last-child {
  color: rgba(0, 0, 0, 0.65);
}
.infomation_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 40px 24px 0;
}
.paginaton {
  height: 92px;
  text-align: right;
  padding: 24px;
}
.el-pager li {
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  border-radius: 6px;
}
.el-pager li.active + li {
  border-left: 1px solid #d9d9d9;
}
.infomation {
  height: 46px;
  line-height: 46px;
  border-bottom: 1px dashed #dadada;
  font-size: 16px;
  color: #333333;
  display: flex;
}
.infomation_icon {
  width: 6px;
  height: 12px;
  position: relative;
  top: 16px;
}
.infomation_txt {
  margin-left: 20px;
  width: 1000px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.infomation_date {
  margin-left: 20px;
  width: 100px;
  text-align: right;
}
</style>



